}

@-webkit-keyframes animate
{
  0%
  {
    -webkit-transform: skewY(5deg);
    transform: skewY(5deg);
  }

  50%
  {
    -webkit-transform: skewY(-5deg);
    transform: skewY(-5deg);
  }

  100%
  {
    -webkit-transform: skewY(5deg);
    transform: skewY(5deg);
  }
}

@keyframes animate
{
  0%
  {
    -webkit-transform: skewY(5deg);
    transform: skewY(5deg);
  }

  50%
  {
    -webkit-transform: skewY(-5deg);
    transform: skewY(-5deg);
  }

  100%
  {
    -webkit-transform: skewY(5deg);
    transform: skewY(5deg);
  }
}

